<template>
  <div>
    <label id="container">提交文件
      <input type="file" id="btn" name="file">
    </label>
  </div>
</template>

<script>
  import qiniu from '@/util/qiniu'

  export default {
    name: "upload-btn",
    data() {
      return {
        url: ''
      }
    },
    methods: {
      getToken() {
        let that = this
        this.$axios.qiniuGet().then(res => {
          that.token = res.data
          qiniu({token: res.data}, function (data) {
            that.url = data.url
            console.log(data);
            that.$emit('uploadSuccess', that.url)
          })
        })

      }
    },
    created() {
      this.$nextTick(() => {
        this.getToken()
      })
    }
  }
</script>

<style scoped>
  #container{
    width: 75px;
    heigth:50px;
    background:#409eff;
    display: block;
    border-radius: 4px;
    border: 1px solid #409eff;
    text-align:center;
    cursor: pointer;
  }
  #btn {
    display: none;
  }
</style>



