<template>
  <div class="newsadd">
    <el-form :model="formData" label-width="80px">
      <el-form-item label="title">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="id">
        <el-input v-model="formData.id"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z1.qiniup.com"
          :show-file-list="false"
          :on-success="upload"
          :data="{token: token}"
        >
          <img v-if="formData.img" :src="formData.img" class="avatar">
          <i v-else class=" el-upload  el-icon-plus  avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="content">
        <el-input v-model="formData.content"></el-input>
      </el-form-item>
      <el-form-item label="site">
        <el-input v-model="formData.site"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="danger" @click="submit" v-if="this.$route.name == '工程添加页'">保存</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="danger" @click="edit" v-if="this.$route.name == '工程编辑页'">确定编辑</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import uploadBtn from '@/components/uploadBtn'
  import editor from '@/components/editor/index'
  import qiniu from '@/util/qiniu'
  export default {
    name: "add",
    components:{
      editor,
      uploadBtn
    },
    data() {
      return {
        formData: {
          title: '',
          id:'',
          img:'',
          content:'',
          site:''
        },
        token:''
      }
    },
    methods:{
      edit() {
        let params = {
          ...this.formData,
          nid: this.$route.query.id
        }
        this.$axios.post('updateProjects', params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: '新闻编辑成功',
              type: 'success'
            })
          } else {
            this.$message({
              message: res.msg,
              type: 'warning'
            })
          }
        })
        this.$router.push('/projects')
      },
      submit(){
        this.$axios.post('addProjects',this.formData).then(res => {
          if(res.code==200){
            this.$message({
              message: '工程添加成功',
              type: 'success'
            });
          }else{
            this.$message({
              message: res.msg,
              type: 'warning'
            });
          }
          setTimeout(() =>{
            this.$router.push('/projects')
          },1500)
        })
      },
      upload(res){
        this.formData.img = "http://image.yaojunrong.com/"+res.key
      },
      getText(val){
        this.formData.site = val
      },
      getToken(){
        let that = this
        this.$axios.qiniuGet().then(res =>{
          that.token = res.data
          console.log(res);

        })
      },
      getUrl(url){
        this.formData.content += `<img src="${url}"/>`
      },
      getData(){

        this.$axios.get('getProjects',{id:this.$route.query.id}).then(res =>{
          this.formData = res.data[0]
        })
      }
    } ,
    created(){
      if(this.$route.name == '工程编辑页'){
        this.getData()
      }
      this.getToken()

    }
  }
</script>

<style scoped>

  .newsadd {
    margin-top: 100px;
    margin-left: 210px;
  }

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

</style>



